:root {
  --black-raw: 0, 0, 0;
  --white-raw: 255, 255, 255;
  --accent_color-raw: 86, 111, 207;
  --danger_color-raw: 221, 0, 0;
  --black: rgb(var(--black-raw));
  --white: rgb(var(--white-raw));
  --accent_color: rgb(var(--accent_color-raw));
  --danger_color: rgb(var(--danger_color-raw));

  --black_2:  rgba(var(--black-raw), 0.02);
  --black_4:  rgba(var(--black-raw), 0.04);
  --black_5:  rgba(var(--black-raw), 0.05);
  --black_10: rgba(var(--black-raw), 0.1);
  --black_15: rgba(var(--black-raw), 0.15);
  --black_20: rgba(var(--black-raw), 0.20);
  --black_40: rgba(var(--black-raw), 0.40);

  --sand_0: #FDFDFC;
  --sand_100: #F9F8F6;
  --sand_200: #F0EFEB;
  --sand_300: #E1DED5;
  --sand_400: #CAC5B8;
  --sand_500: #A49F95;
  --sand_600: #6B6860;
  --sand_700: #45433E;
  --sand_800: #262522;
  --sand_900: #12110D;

  --plum_500: #CC6FBE;
  --plum_400: #E37BD3;
  --plum_100: #F7D6F2;
  --plum_0: #FBF2FC;

  --persimmon_0: #FFF1EE;
  --persimmon_300: #FF9F89;
  --persimmon_400: #FF7557;

  --canteloupe_0: #FFF6EF;
  --canteloupe_100: #FFE4D0;
  --canteloupe_400: #FFA663;
  --canteloupe_500: #E69559;
  --canteloupe_600: #CC854F;
  --canteloupe_700: #B37445;

  --kale_0: #F4F9F9;
  --kale_400: #57B7AB;

  --red_1: #FFF1F0;
  --red_3: #FFCCC7;
  --red_7: #CF1322;
  --red_10: #5C0011;

  --grape_0: #F0F3FE;
  --grape_100: #D4DBFB;
  --grape_500-raw: 86, 111, 207;
  --grape_500: rgb(var(--grape_500-raw));
  --grape_600: #576CC1;
  --grape_700: #4C5FA9;
  --grape_800: #37447A;

  --primary_link: var(--grape_500);
  --danger_color: var(--red_7);
  --danger_color--faded: var(--red_3);
  --success_color: var(--kale_400);

  --surface-background: var(--sand_0);
  --surface-outline-outline: var(--sand_300);
  --surface-border: var(--sand_300);
  --surface-surface: var(--sand_100);
  --surface-base: var(--sand_100);
  --surface-on: var(--sand_900);
  --surface-on-dim: var(--sand_600);
  --surface-bright: var(--grape_0);
  --surface-border-light: var(--sand_200);
  --surface-on-surface: var(--sand_700);
  --surface-icon-on-base: var(--sand_700);
  --surface-text-dimmer: var(--sand_600);
  --surface-text-muted: var(--sand_600);
  --surface-surface-text-base: var(--sand_900);
  --surface-outline-outline-light: var(--sand_200);
  --surface-outline-outline-dark: var(--sand_600);
  --inverted-surface: var(--sand_900);
  --neutral-palette-light-theme-surface-on-surface: var(--sand_900);
  --neutral-variant-palette-light-theme-outline-opacity-outline-opacity16: var(--sand_200);
  --neutral-palette-light-theme-surface-surface: var(--sand_100);
  --neutral-palette-light-theme-surface-text: var(--sand_900);
  --volcano-palette-tonal-palette-volcano70: var(--persimmon_400);
  --primary-primary: var(--grape_500);
  --primary-background: var(--grape_0);
  --primary-text-on-background: var(--sand_0);
  --primary-bright: var(--grape_500);
  --primary-bright-secondary: var(--grape_0);
  --primary-bright-tertiary: var(--grape_100);
  --primary-text-on-background-dark: var(--grape_800);

  --accent_color: var(--grape_500);
  --colors-surface-border: var(--sand_300);
  --colors-surface-icon-on-base: var(--sand_700);
  --colors-surface-text: var(--sand_700);

  --primary-button-waiting-animation-bg: repeating-linear-gradient(-63.43deg, hsla(0, 0%, 100%, 0.2) 1px, transparent 2px, transparent 6px, hsla(0, 0%, 100%, 0.2) 7px, hsla(0, 0%, 100%, 0.2) 12px);
  --button-waiting-animation-bg: repeating-linear-gradient(-63.43deg,hsla(60,20%,99%,.2) 1px,#efefef 2px,#efefef 6px,hsla(60,20%,99%,.2) 7px,hsla(60,20%,99%,.2) 12px);
}